import React from 'react';
import "./FriendsCard.scss"
import {NavLink} from "react-router-dom";
import userPlaceholder from "../../../assets/images/userPlaceholder.jpg";


const FriendsCard = (props) => {
    let FriendsItems = ''
    let isPhoto
    if (props.friends) {
        console.log(props.friends)
        FriendsItems = props.friends.map(friend => {
            isPhoto = friend.photos?.large ? friend.photos.large : userPlaceholder
            return (
                <NavLink to={`/profile/${friend.id}`}>
                    <div className="item" key={friend.id}>
                        <div className="person_image">
                            <img src={isPhoto} alt="user"/>
                        </div>
                        <p className="name">{friend.name}</p>
                    </div>
                </NavLink>
            )
        })
    }

    return (<div className="friends card">
        <div className="card__title">
            <h4>Friends</h4>
        </div>

        <div className="friends_items">
            {FriendsItems}
        </div>
    </div>)
};

export default FriendsCard;