import React from 'react';

import "./ProfilePerson.scss";

import Skeleton from "@material-ui/lab/Skeleton";
import userPlaceholder from "../../../assets/images/userPlaceholder.jpg";
import ProfileStatus from "./ProfileStatus/ProfileStatus";


export const ProfilePerson = (props) => {

    const isPhoto = props.profile && props.profile?.photos.large
        ? props.profile.photos.large
        : userPlaceholder

    const EditButton = () => {
        return (
            <div className="profile-info--btn">
                <button>Edit Profile</button>
            </div>
        )
    }

    const SendMessage = () => {
        return (
            <div className="profile-info--btn">
                <button>Send message</button>
            </div>
        )
    }

    return (
        <div className="container profile-person">
            <div className="profile-person__header">
                <div className="profile-person__picture">
                    {props.isLoading ? <Skeleton variant="circle" width={175} height={175}/>
                        : <img src={isPhoto} alt="profile_pic"/>}
                </div>

                <div className="info">
                    <div className="nameAbout">
                        <div className="fullName">
                            {props.isLoading ?
                                <Skeleton variant="text" width={150} height={20}/> : props.profile.fullName}
                        </div>
                        <ProfileStatus isLoading={props.isLoading}
                                       status={props?.status || ''}
                                       updateStatus={props.updateStatus}
                                       itsMe={props.itsMe}
                        />
                    </div>

                    {props.itsMe ? <EditButton /> : <SendMessage />}

                </div>
            </div>
        </div>
    );
}
