import React, {useEffect, useState} from "react"

import "./Users.scss"

import UserItem from "./UserItem/UserItem";
import Pagination from '@material-ui/lab/Pagination';
import SearchIcon from "@material-ui/icons/Search";

import Preloader from "../../assets/images/Loading"
import HeaderContainer from "../Header/HeaderContainer";
import profileBackground from "../../assets/images/background-profile.jpg";

import userPlaceholder from "../../assets/images/userPlaceholder.jpg";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

const Users = (props) => {
    //Prepare Elements
    let isPhoto
    isPhoto = props.profilePhoto?.large ? props.profilePhoto.large : userPlaceholder
    const usersElements = props.users.map((user) => <UserItem key={"UserItem " + user.id}
                                                              id={user.id}
                                                              photo={user.photos}
                                                              fullName={user.name}
                                                              followed={user.followed}
                                                              nowFollowingUsers={props.nowFollowingUsers}
                                                              toggleFollowThunk={props.toggleFollowThunk}
    />)
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize)
    // HandleFunctions
    const handleClickCheckBox = () => {
        toggleOnlyFriends(!onlyFriends)
    }
    const handleOnChangeUserPage = (_, pageNumber) => {
        props.changeUsersPage(pageNumber, props.pageSize, onlyFriends)
    }
    const handleClickSearch = () => {
        props.searchUser(props.currentPage, props.pageSize, onlyFriends, searchTerm)
    }

    //State, Hooks, etc
    const [onlyFriends, toggleOnlyFriends] = React.useState(false)
    const [searchTerm, setSearchTerm] = useState('')

    useEffect(() => {
        props.getUsers(props.currentPage, props.pageSize, onlyFriends)
    }, [onlyFriends])


    return (
        <div className="pageBlock">
            <div className="Users">
                <HeaderContainer/>

                <div className="profile-background">
                    <img src={profileBackground} alt=""/>
                </div>

                <div className="container profile-person">
                    <div className="profile-person__header">

                        <div className="profile-person__picture">
                            <img src={isPhoto} alt="profile_pic"/>
                        </div>

                        <div className="header__inner">
                            <div className="leftSide">
                                <div className="user-search">
                                    <input type="text"
                                           placeholder="Search"
                                           className="user-search-field"
                                           onChange={e => setSearchTerm(e.target.value)}
                                    />
                                    <button className="search-btn" onClick={handleClickSearch}>
                                        <SearchIcon/>
                                    </button>
                                </div>

                                <div className="checkOnlyFriends">
                                    <FormControlLabel
                                        control={
                                            <Checkbox
                                                checked={onlyFriends}
                                                onChange={handleClickCheckBox}
                                                name="checkedB"
                                                color="primary"
                                            />
                                        }
                                        label="Only Friends"
                                    />
                                </div>
                            </div>



                            <div className="pagination">
                                <Pagination count={pagesCount} page={props.currentPage} onChange={handleOnChangeUserPage}
                                            siblingCount={3} boundaryCount={1} defaultPage={1} defaultValue={1}
                                            variant="outlined" color={"secondary"} shape={"rounded"}/>
                            </div>
                        </div>
                    </div>
                </div>


                <div className="blockPeople">
                    <div className="container">
                        <div className="userList">
                            {props.isFetching ? <Preloader/> : usersElements}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Users


