import React, {useEffect} from 'react';

import "./Profile.scss";
import Profile from "./Profile";
import {addPost, getStatus, getUserProfile, updateStatus} from "../../redux/profilePageReducer";

import {connect} from "react-redux";
import {withRouter} from "react-router-dom/";
import {withAuthRedirect} from "../../HOC/withAuthRedirect";
import {compose} from "redux";

function ProfileContainer(props) {

    useEffect(()=> {
        let reqUserId = props.match.params.userId
        const myUserId = props.authorizedUserId
        if (!reqUserId) {
            reqUserId = myUserId
            if (!reqUserId) {
                props.history.push("/login")
            }
        }
        props.getUserProfile(reqUserId, reqUserId === myUserId)
        props.getStatus(reqUserId)
    }, [props.match.params.userId])

    return <Profile {...props}/>
}

const mapStateToProps = (state) => {
    return ({
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        authorizedUserId: state.auth.userId,
        isAuth: state.auth.isAuth,
        isLoading: state.profilePage.isLoading,
        itsMe: state.profilePage.itsMe,
        friends: state.profilePage.friends
    })
}

const mapDispatchToProps = {
    getUserProfile,
    getStatus,
    updateStatus,
    addPost,
}


const ComposedComponent = compose(
    connect(mapStateToProps, mapDispatchToProps),
    withRouter,
    withAuthRedirect
)(ProfileContainer)

export default ComposedComponent
