import React from 'react'

import "./Dialogs.scss"
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import HeaderContainer from "../Header/HeaderContainer";
import profileBackground from "../../assets/images/background-profile.jpg";
import {useFormik} from "formik";
import Skeleton from "@material-ui/lab/Skeleton";

import SendIcon from '@material-ui/icons/Send';
import userPlaceholder from "../../assets/images/userPlaceholder.jpg";

const Dialogs = (props) => {
    //Prepare Elements
    let isPhoto
    isPhoto = props.profilePhoto?.small ? props.profilePhoto.small : userPlaceholder
    isPhoto = props.profilePhoto?.large ? props.profilePhoto.large : userPlaceholder

    let dialogsElements = props.state.dialogs.map((d, idx) => <DialogItem key={"dialog-" + idx} id={d.id}
                                                                          name={d.name}/>)
    let messagesElements = props.state.messages.map((m, idx) => <Message key={"message-" + idx} id={m.id}
                                                                         message={m.message} me={m.me}/>)

    const formik = useFormik({
        initialValues: {
            messageBody: '',
        },
        onSubmit: (values, {setSubmitting}) => {
            setTimeout(() => {
                if (values.messageBody) {
                    props.sendMessage(values.messageBody)
                    setSubmitting(false)
                }
            }, 500)
        }
    })

    return (
        <div>
            <HeaderContainer/>

            <div className="profile-background">
                <img src={profileBackground} alt=""/>
            </div>

            <div className="container profile-person">
                <div className="profile-person__header dialogsWindow">
                    <div className="profile-person__picture">
                        {props.isLoading ? <Skeleton variant="circle" width={175} height={175}/>
                            : <img src={isPhoto} alt="profile_pic"/>}
                    </div>

                    <div className="dialogsUsersList">
                        <div className="dialogsUsersList__inner">
                            {dialogsElements}
                        </div>
                    </div>

                    <div className="dialogsWindow_inner">
                        <div className="dialogsWindow__curUser">
                            <div className="dialogsWindow__curUser--picture">
                                <img src={isPhoto} alt="user"/>
                            </div>
                            <div className="dialogsWindow__curUser--username">
                                {"Sanya"}
                            </div>
                        </div>

                        <div className="dialogsWindow__chat">
                            {messagesElements}
                        </div>

                        <form onSubmit={formik.handleSubmit}>
                            <div className="dialogsWindow__sendMessage">
                                <div className='dialogsWindow__wrapper'>
                                    <input
                                        type="text"
                                        id="messageBody"
                                        name="messageBody"

                                        value={formik.values.messageBody}
                                        onChange={formik.handleChange}

                                        placeholder="Write a message..."
                                        maxLength={90}
                                        autoComplete="off"
                                    />
                                </div>

                                <div className="dialogsWindow__sendMessage-icon" onClick={formik.handleSubmit}>
                                    <SendIcon/>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Dialogs
